

























import Vue from "vue";
import LevelExplorer from "@/components/imaging/LevelExplorer.vue";
import TransactionExplorer from "@/components/imaging/TransactionExplorer.vue";
import IconManager from "@/components/imaging/IconManager.vue";

export default Vue.extend({
  name: "ImagingTuning",

  components: {
    LevelExplorer,
    TransactionExplorer,
    IconManager
  },

  computed: {
    getApplicationName() {
      return this.$store.state.applicationName;
    }
  },

  mounted() {
    this.application = this.$store.state.applicationName;
  },

  data: () => ({
    application: "",

    step: 1,
    tab: 0,

    items: [
      {
        view: "LevelExplorer",
        name: "Level Manager",
        title: "Review the configuration of the ",
        icon: "mdi-folder-cog"
      },
      {
        view: "TransactionExplorer",
        name: "Transaction",
        title: "Explore the transaction ",
        icon: "mdi-apple-safari"
      },
      {
        view: "IconManager",
        name: "Icon Manager",
        title: "Manage icons ",
        icon: "mdi-file-image-outline"
      }
    ]
  }),

  watch: {
    getApplicationName(newApp) {
      this.application = newApp;
    }
  }
});
