

























































































































































import Vue from "vue";

import {
  ApplicationController,
  ApplicationRecord
} from "@/api/applications/application.controller";

import Administration from "@/components/screens/administration/Administration.vue";
import ImagingTuning from "@/components/screens/imagingTuning/ImagingTuning.vue";
import Enrichment from "@/components/screens/enrichment/Enrichment.vue";
import Frameworks from "@/components/screens/frameworks/Frameworks.vue";
import { Configuration } from "./Configuration";
import { UtilsController } from "./api/utils/utils.controller";
import Reporting from "@/components/screens/reports/Reporting.vue";
import AnalysisChecker from "@/components/screens/recommendation/AnalysisChecker.vue";

export default Vue.extend({
  name: "Application",

  components: {
    Reporting,
    Enrichment,
    Administration,
    AnalysisChecker,
    Frameworks,
    ImagingTuning
  },

  mounted() {
    this.getApplicationList();
    this.healthcheck();
  },

  computed: {
    getCurrentView() {
      return this.$store.state.currentView;
    }
  },

  data: () => ({
    tab: 0,
    currentScreen: "Reporting",

    items: [
      { name: "Reporting", screen: "Reporting", icon: "mdi-file" },
      {
        name: "Analysis Checker",
        screen: "AnalysisChecker",
        icon: "mdi-file-cad"
      },
      {
        name: "Enrichment",
        screen: "Enrichment",
        icon: "mdi-hexagon-multiple"
      },
      {
        name: "Frameworks",
        screen: "Frameworks",
        icon: "mdi-package-variant-closed"
      },
      { name: "Imaging tuning", screen: "ImagingTuning", icon: "mdi-merge" }
    ],

    loadingApplication: true as boolean,
    applicationName: "" as string,
    applicationList: [] as string[],

    onlineDatabase: false
  }),

  methods: {
    /** Change the state of the application **/
    changeApplication(application: string) {
      this.applicationName = application;
      // Update store properties
      this.$store.state.applicationName = application;
    },

    getApplicationList() {
      this.loadingApplication = true;
      ApplicationController.getListApplications().then((res: string[]) => {
        this.applicationList = res;
        if (res.length != 0) {
          this.changeApplication(res[0]);
        } else {
          this.applicationName = "No Application found";
        }

        this.loadingApplication = false;
      });
    },

    simpleHealthCheck() {
      UtilsController.healthCheck()
        .then((res: boolean) => {
          this.onlineDatabase = res;
        })
        .catch(err => {
          this.onlineDatabase = false;
        });
    },

    healthcheck() {
      UtilsController.healthCheck()
        .then((res: boolean) => {
          this.onlineDatabase = res;
        })
        .catch(err => {
          this.onlineDatabase = false;
        })
        .finally(() => {
          setTimeout(
            function() {
              this.healthcheck();
            }.bind(this),
            5000
          );
        });
    },

    changeView(view: string) {
      console.log("Go to administration");
      this.$store.state.currentView = view;
      this.currentScreen = view;
    },

    logout() {
      Configuration.deleteProperties();
      document.location.reload();
    }
  },

  watch: {
    applicationName: function() {
      this.changeApplication(this.applicationName);
    },

    tab: function() {
      this.$store.state.currentView = this.items[this.tab].name;
      this.currentScreen = this.items[this.tab].screen;
    },

    getCurrentView(newView) {
      for (let i = 0; i < this.items.length; i++) {
        if (this.items[i].name === newView) {
          // Found a view with a matching name
          this.tab = i;
        }
      }

      // Do nothing if the view wasn't found
    }
  }
});
